	module.exports = {"data":{"remark":{"html":"<h2 id=\"command-line-interface\"><a href=\"#command-line-interface\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Command Line Interface</h2>\n<p>The easiest way to get up and running with PostGraphile is to use the Command Line Interface.</p>\n<p>Install PostGraphile globally via npm:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">npm</span> <span class=\"token function\">install</span> -g postgraphile\n</code></pre>\n      </div>\n<p>This will make the <code>postgraphile</code> command available. You can then run:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>postgraphile -c postgres://localhost/mydb -s public -a -j</code></pre>\n      </div>\n<p>where <code>-c</code> is the connection string (defaults to postgres://localhost/), <code>-s</code> is the schema name (defaults to \"public\"), <code>-a</code> enables Relay support and <code>-j</code> enables dynamic JSON.</p>\n<h3 id=\"cli-options\"><a href=\"#cli-options\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>CLI options</h3>\n<p>There are more CLI options available to customise the GraphQL server:</p>\n<ul>\n<li><code>-h</code>, <code>--help</code><br>\noutput usage information</li>\n<li><code>-V</code>, <code>--version</code><br>\noutput the version number</li>\n<li><code>-c</code>, <code>--connection &#x3C;string></code><br>\nthe PostgreSQL connection string, if not provided it will be inferred from your environment, example: <code>postgres://user:password@domain:port/dbname</code></li>\n<li><code>-s</code>, <code>--schema &#x3C;string></code><br>\na PostgreSQL schema to be introspected. Use commas to define multiple schemas</li>\n<li><code>-w</code>, <code>--watch</code><br>\nwatches the PostgreSQL schema for changes and reruns introspection if a change was detected</li>\n<li><code>-n</code>, <code>--host &#x3C;string></code><br>\nthe hostname to be used. Defaults to <code>localhost</code></li>\n<li><code>-p</code>, <code>--port &#x3C;number></code><br>\nthe port to be used. Defaults to 5000</li>\n<li><code>-m</code>, <code>--max-pool-size &#x3C;number></code><br>\nthe maximum number of clients to keep in the PostgreSQL pool. defaults to 10</li>\n<li><code>-r</code>, <code>--default-role &#x3C;string></code><br>\nthe default PostgreSQL role to use when a request is made. supercedes the role used to connect to the database</li>\n<li><code>-q</code>, <code>--graphql &#x3C;path></code><br>\nthe route to mount the GraphQL server on. defaults to <code>/graphql</code></li>\n<li><code>-i</code>, <code>--graphiql &#x3C;path></code><br>\nthe route to mount the GraphiQL interface on. defaults to <code>/graphiql</code></li>\n<li><code>-b</code>, <code>--disable-graphiql</code><br>\ndisables the GraphiQL interface. overrides the GraphiQL route option</li>\n<li><code>-o</code>, <code>--cors</code><br>\nenable generous CORS settings. this is disabled by default, if possible use a proxy instead</li>\n<li><code>-a</code>, <code>--classic-ids</code><br>\nuse classic global id field name; required to support Relay</li>\n<li><code>-j</code>, <code>--dynamic-json</code><br>\nenable dynamic JSON in GraphQL inputs and outputs (uses stringified JSON by default)</li>\n<li><code>-M</code>, <code>--disable-default-mutations</code><br>\ndisable default mutations, mutation will only be possible through custom PostgreSQL volatile functions</li>\n<li><code>-l</code>, <code>--body-size-limit &#x3C;string></code><br>\nset the maximum size of JSON bodies that can be parsed (default 100kB) The size can be given as a human-readable string, such as '200kB' or '5MB' (case insensitive).</li>\n<li><code>-e, --jwt-secret &#x3C;string></code><br>\nthe secret to be used when creating and verifying JWTs. if none is provided auth will be disabled</li>\n<li><code>-A, --jwt-audiences &#x3C;string></code><br>\na comma separated list of audiences your jwt token can contain. If no audience is given the audience defaults to <code>postgraphql</code></li>\n<li><code>--jwt-role &#x3C;string></code><br>\na comma separated list of strings that create a path in the jwt from which to extract the postgres role. if none is provided it will use the key <code>role</code> on the root of the jwt.</li>\n<li><code>-t</code>, <code>--jwt-token-identifier &#x3C;identifier></code><br>\nthe PostgreSQL identifier for a composite type that will be used to create JWT tokens</li>\n<li><code>--export-schema-json [path]</code><br>\nenables exporting the detected schema, in JSON format, to the given location. The directories must exist already, if the file exists it will be overwritten.</li>\n<li><code>--export-schema-graphql [path]</code><br>\nenables exporting the detected schema, in GraphQL schema format, to the given location. The directories must exist already, if the file exists it will be overwritten.</li>\n<li><code>--show-error-stack [setting]</code><br>\nshow JavaScript error stacks in the GraphQL result errors</li>\n<li><code>--extended-errors &#x3C;string></code><br>\na comma separated list of extended PostgreSQL error fields to display in the GraphQL result. Possible fields: <code>'hint', 'detail', 'errcode'</code>. Default: none</li>\n</ul>","frontmatter":{"path":"/postgraphile/usage-cli/","title":"PostGraphile CLI"}},"nav":{"edges":[{"node":{"id":"/Users/benjiegillam/Documents/graphile.org/src/data/nav.json absPath of file [0] >>> JSON","name":"graphile-build","sections":[{"id":"guides","title":"Overview"},{"id":"library-reference","title":"Using the Library"},{"id":"plugin-reference","title":"Building a Plugin"}],"pages":[{"to":"/graphile-build/getting-started/","title":"Getting Started","sectionId":"guides"},{"to":"/graphile-build/plugins/","title":"Plugins","sectionId":"guides"},{"to":"/graphile-build/hooks/","title":"Hooks","sectionId":"guides"},{"to":"/graphile-build/look-ahead/","title":"Look Ahead","sectionId":"guides"},{"to":"/graphile-build/graphile-build/","title":"graphile-build","sectionId":"library-reference"},{"to":"/graphile-build/schema-builder/","title":"SchemaBuilder","sectionId":"library-reference"},{"to":"/graphile-build/plugin-options/","title":"Options","sectionId":"library-reference"},{"to":"/graphile-build/default-plugins/","title":"Default Plugins","sectionId":"library-reference"},{"to":"/graphile-build/omitting-plugins/","title":"Omitting Plugins","sectionId":"guides"},{"to":"/graphile-build/all-hooks/","title":"All Hooks","sectionId":"plugin-reference"},{"to":"/graphile-build/build-object/","title":"Build Object","sectionId":"plugin-reference"},{"to":"/graphile-build/context-object/","title":"Context Object","sectionId":"plugin-reference"},{"to":"/graphile-build/schema-builder/","title":"SchemaBuilder","sectionId":"library-reference"}]}},{"node":{"id":"/Users/benjiegillam/Documents/graphile.org/src/data/nav.json absPath of file [1] >>> JSON","name":"postgraphile","sections":[{"id":"overview","title":"Overview"},{"id":"usage","title":"Usage"}],"pages":[{"to":"/postgraphile/introduction/","title":"Introduction","sectionId":"overview"},{"to":"/postgraphile/performance/","title":"Performance","sectionId":"overview"},{"to":"/postgraphile/connections/","title":"Connections","sectionId":"overview"},{"to":"/postgraphile/filtering/","title":"Filtering","sectionId":"overview"},{"to":"/postgraphile/relations/","title":"Relations","sectionId":"overview"},{"to":"/postgraphile/crud-mutations/","title":"CRUD Mutations","sectionId":"overview"},{"to":"/postgraphile/computed-columns/","title":"Computed Columns","sectionId":"overview"},{"to":"/postgraphile/custom-queries/","title":"Custom Queries","sectionId":"overview"},{"to":"/postgraphile/custom-mutations/","title":"Custom Mutations","sectionId":"overview"},{"to":"/postgraphile/security/","title":"Security","sectionId":"overview"},{"to":"/postgraphile/introspection/","title":"Introspection","sectionId":"overview"},{"to":"/postgraphile/usage-cli/","title":"CLI Usage","sectionId":"usage"},{"to":"/postgraphile/usage-library/","title":"Library Usage","sectionId":"usage"},{"to":"/postgraphile/usage-schema/","title":"Schema-only Usage","sectionId":"usage"}]}},{"node":{"id":"/Users/benjiegillam/Documents/graphile.org/src/data/nav.json absPath of file [2] >>> JSON","name":"graphile-build-pg","sections":[{"id":"usage","title":"Usage"}],"pages":[{"to":"/postgraphile/settings/","title":"Settings","sectionId":"usage"}]}}]}},"pathContext":{"layout":"page"}}

